import Vue from 'vue'
import Router from 'vue-router'
import firstPage from '../content/first-page-content.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/fff',
      name: 'first',
      component: firstPage
    }
  ]
})



