import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    bet: [],
    allOdds: [],
    printItems: []
  },
  getters: {
    getBet(state) {
      return state.bet
    },
    getDoobleOds(state) {
      return state.allOdds.doubleOdds
    },
    getSingleOddsNegative(state) {
      if (state.allOdds.singleOdds)
        return state.allOdds.singleOdds.negativeOdds
      else
        []
    },
    getSingleOddsPositive(state) {
      if (state.allOdds.singleOdds)
        return state.allOdds.singleOdds.positiveOdds
      else
        []
    },
    getDoubleOpportunity(state) {
      return state.allOdds.doubleOpportunity
    },
    getPrintItems(state) {
      return state.printItems
    }

  },
  mutations: {
    setbet(state, bet) {
      state.allOdds = bet
    },
    setPrintItems(state, item) {
      state.printItems.push(item)
    }

  },
  actions: {
    getOdds({commit}) {
      axios.get('/v1/odds?gameID=1', {headers: {'Access-Control-Allow-Origin': true}}).then(function (res) {
        commit('setbet', res.data)
      })
    }
  }


})

export default store



